<script>
import { ref, onMounted } from 'vue'
import axios from 'axios'
import Swiper from 'swiper/swiper-bundle'
import Tilt from 'vanilla-tilt-vue'
import 'swiper/swiper-bundle.css'
export default {
  components: {
    Tilt
  },
  props: ['pageActive'],
  setup (props, { emit }) {
    const data = ref([])
    const swiper = ref(null)
    const circleItem = ref(null)
    const ItemNum = ref(null)
    const url = './json/data.json'
    onMounted(() => {
      axios.get(url)
        .then((res) => {
          data.value = res.data.data
        })
        .then(() => {
          swiper.value = new Swiper('.swiper-container', {
            speed: 400,
            autoplay: {
              delay: 5000,
              disableOnInteraction: false,
              stopOnLastSlide: true
            },
            slidesPerView: 1,
            spaceBetween: 30,
            mousewheel: true,
            allowTouchMove: true,
            navigation: {
              nextEl: '.swiper-next',
              prevEl: '.swiper-prev'
            },
            breakpoints: {
              1200: {
                allowTouchMove: false
              }
            },
            on: {
              init (swiper) {
                const backgroundColor = swiper.slides[swiper.activeIndex].attributes['data-color'].value
                const textColor = swiper.slides[swiper.activeIndex].attributes['data-text-black'].value
                const directionVal = 'top'
                emit('push-data', backgroundColor, textColor, directionVal)
                ItemNum.value.innerText = (swiper.activeIndex < 10) ? ('0' + swiper.activeIndex) : swiper.activeIndex
                circleItem.value.attributes['data-num'].value = swiper.activeIndex
                swiper.autoplay.stop()
              },
              slideChange (swiper) {
                const backgroundColor = swiper.slides[swiper.activeIndex].attributes['data-color'].value
                const textColor = swiper.slides[swiper.activeIndex].attributes['data-text-black'].value
                let directionVal = 'left'
                if (swiper.activeIndex === 0) {
                  swiper.autoplay.stop()
                  directionVal = 'top'
                } else {
                  swiper.autoplay.start()
                  directionVal = 'left'
                }
                emit('push-data', backgroundColor, textColor, directionVal)
                ItemNum.value.innerText = (swiper.activeIndex < 10) ? ('0' + swiper.activeIndex) : swiper.activeIndex
                circleItem.value.attributes['data-num'].value = swiper.activeIndex
              },
              slideChangeTransitionStart () {
                circleItem.value.classList.remove('active')
              },
              slideChangeTransitionEnd () {
                circleItem.value.classList.add('active')
              }
            }
          })
        })
    })
    function openCPage (msg) {
      if (msg) {
        swiper.value.autoplay.stop()
        circleItem.value.classList.remove('active')
      } else {
        swiper.value.autoplay.start()
        circleItem.value.classList.add('active')
      }
    }
    return {
      data,
      swiper,
      circleItem,
      ItemNum,
      openCPage
    }
  }
}
</script>
