<script>
import { ref, watch, onMounted } from 'vue'
import $ from 'jquery'
export default {
  props: ['pageActive'],
  setup (props, { emit }) {
    const page = props

    watch(page, (newVal) => {
      if (newVal.pageActive === 'contact') {
        const letterTimer = ref(null)
        const letterDuration = 50
        const letterDelay = 50
        let letterIndex = -1
        const letterTotal = $('.contact__title').find('.letter').length
        setTimeout(function runLetter () {
          if (letterIndex < (letterTotal - 1)) {
            letterIndex++
            $('.contact__title').find('.letter').eq(letterIndex).addClass('up-action')
            letterTimer.value = setTimeout(runLetter, letterDuration)
          }
        }, letterDelay)
        setTimeout(function () {
          $('.form-inner').addClass('form-inner--show')
          $('.select-list-wrap').addClass('select-list-wrap--show')
        }, 300)
      }
    })

    function openPage (text, go) {
      emit('push-page', text, go)
      $('.contact__title').find('.letter').removeClass('up-action')
      $('.form-inner').removeClass('form-inner--show')
      $('.select-list-wrap').removeClass('select-list-wrap--show')
    }
    onMounted(() => {
      $('.select-list').find('.option').click(function () {
        $('.select-list').find('.option').not($(this)).removeClass('selected')
        $(this).addClass('selected')
      })
    })

    return {
      openPage,
      page
    }
  }
}
</script>
