<script>
import { ref, watch } from 'vue'
import $ from 'jquery'
export default {
  props: ['pageActive'],
  setup (props, { emit }) {
    const page = props

    watch(page, (newVal) => {
      if (newVal.pageActive === 'service') {
        const serviceContentTimer = ref(null)
        const letterTimer = ref(null)
        const letterDuration = 50
        const letterDelay = 50
        let letterIndex = -1
        const letterTotal = $('.service__title').find('.letter').length
        let letterIndex2 = -1
        const letterTotal2 = $('.service__content li').length
        setTimeout(function runLetter2 () {
          if (letterIndex2 < (letterTotal2 - 1)) {
            letterIndex2++
            $('.service__content li').eq(letterIndex2).addClass('up-action')
            serviceContentTimer.value = setTimeout(runLetter2, letterDuration)
          }
        }, letterDelay)
        setTimeout(function runLetter () {
          if (letterIndex < (letterTotal - 1)) {
            letterIndex++
            $('.service__title').find('.letter').eq(letterIndex).addClass('up-action')
            letterTimer.value = setTimeout(runLetter, letterDuration)
          }
        }, letterDelay)
      }
    })

    function openPage (text, go) {
      emit('push-page', text, go)
      $('.service__content li').each(function () {
        $(this).removeClass('up-action')
      })
      $('.service__title').find('.letter').removeClass('up-action')
    }
    return {
      openPage,
      page
    }
  }
}
</script>
