<template>
  <div id="wrap" ref="wrap" class="position-relative" :style="`background-color:${backgroundColor}`" :data-text-black="`${textColor}`">
    <div ref="loadingWrap" class="loading-wrap">
      <div ref="progress" class="progress-num"></div>
    </div>
    <canvas id="canvas"></canvas>
    <div class="bg-img position-absolute" style="opacity: 0;z-index: -1;">
      <img src="@/assets/img/bg-1.png" class="ball__object__index bg-img-index1" alt="" data-velocity="-85">
      <img src="@/assets/img/bg-3.png" class="ball__object__index bg-img-index2" alt="" data-velocity="-30">
      <img src="@/assets/img/bg-2.png" class="ball__object__index bg-img-index3" alt="" data-velocity="-60">
    </div>
    <Header></Header>
    <MenuList @push-page="getPage"></MenuList>
    <Swiper ref="swiperPage" :pageActive="pageActive" @push-data="getBgColor"></Swiper>
    <Ball :pageActive="pageActive" :class="{'Active' : pageActive !== ''}"></Ball>
    <About :class="{'Active' : pageActive === 'about'}" @push-page="getPage" :pageActive="pageActive"></About>
    <Service :class="{'Active' : pageActive === 'service'}" @push-page="getPage" :pageActive="pageActive"></Service>
    <Contact :class="{'Active' : pageActive === 'contact'}" @push-page="getPage" :pageActive="pageActive"></Contact>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import Header from '@/components/Header.vue'
import MenuList from '@/components/MenuList.vue'
import Swiper from '@/components/Swiper.vue'
import About from '@/components/About.vue'
import Service from '@/components/Service.vue'
import Contact from '@/components/Contact.vue'
import Ball from '@/components/Ball.vue'
import $ from 'jquery'
export default {
  components: {
    Header,
    MenuList,
    Swiper,
    About,
    Service,
    Contact,
    Ball
  },
  setup () {
    // 載入效果
    const wrap = ref(null)
    const progress = ref(null)
    onMounted(() => {
      let i = 1
      add()
      function add () {
        var timer = setTimeout(function () {
          add()
        }, 300 / 100)
        progress.value.innerHTML = i
        i < 100 ? i++ : clearTimeout(timer)
      }
      setTimeout(() => {
        wrap.value.classList.add('loading-done')
      }, 600)
    })

    // 切換效果
    const backgroundColor = ref(null)
    const textColor = ref(null)
    const direction = ref(null)
    function getBgColor (BgColor, TxtColor, directionVal) {
      backgroundColor.value = BgColor
      textColor.value = TxtColor
      direction.value = directionVal
    }

    // 背景ball
    const bgImg = ref(null)
    const ball = ref([])
    function getBall () {
      bgImg.value = Array.from(document.querySelectorAll('.ball__object__index'))
      ball.value = bgImg.value.map(item => {
        return {
          itemHeight: item.clientHeight,
          itemWidth: item.clientWidth,
          initialPosition: {
            x: item.offsetLeft,
            y: item.offsetTop
          },
          position: {
            x: item.offsetLeft,
            y: item.offsetTop
          },
          velocity: item.dataset.velocity
        }
      })
    }
    // canvas
    const canvasElement = ref(null)
    const context = ref(null)
    let time = 0
    const continueAnimating = ref(true)
    const windowWidth = ref(window.innerWidth)
    const windowHeight = ref(window.innerHeight)
    function onResize () {
      windowWidth.value = window.innerWidth
      windowHeight.value = window.innerHeight
      const doc = document.documentElement
      doc.style.setProperty('--app-height', `${window.innerHeight}px`)
    }
    onMounted(() => {
      setTimeout(() => {
        getBall()
        canvasElement.value = document.querySelector('#canvas')
        canvasElement.value.width = windowWidth.value
        canvasElement.value.height = windowHeight.value
        context.value = canvasElement.value.getContext('2d')
        requestAnimationFrame(render)
      }, 300)
    })
    function motion (offset) {
      for (let i = 0; i < ball.value.length; i++) {
        if (direction.value === 'top') {
          ball.value[i].position.y = ball.value[i].position.y + ball.value[i].velocity / 1000 * offset
          if (ball.value[i].position.y < -ball.value[i].itemHeight) {
            ball.value[i].position.y = windowHeight.value + ball.value[i].itemHeight
          }
          $({ countNum: ball.value[i].position.x }).animate({ countNum: ball.value[i].initialPosition.x }, {
            duration: 100,
            easing: 'swing',
            progress: function () {
              ball.value[i].position.x = this.countNum
            }
          })
        } else {
          ball.value[i].position.x = ball.value[i].position.x + (-80) / 1000 * offset
          if (ball.value[i].position.x < -(ball.value[i].itemWidth)) {
            ball.value[i].position.x = windowWidth.value + ball.value[i].itemWidth
          }
        }
      }
    }
    function render (t) {
      context.value.clearRect(0, 0, windowWidth.value, windowHeight.value)
      motion(t - time)
      var img = document.querySelectorAll('.ball__object__index')
      for (let i = 0; i < img.length; i++) {
        context.value.drawImage(img[i], ball.value[i].position.x, ball.value[i].position.y, ball.value[i].itemWidth, ball.value[i].itemHeight)
      }
      time = t
      if (!continueAnimating.value) return
      requestAnimationFrame(render)
    }
    // 頁面切換
    const pageActive = ref('')
    const swiperPage = ref(null)
    const stoppedAt = ref(null)
    function getPage (page, go) {
      continueAnimating.value = go
      if (continueAnimating.value) {
        time += window.performance.now() - stoppedAt.value
        requestAnimationFrame(render)
      } else {
        stoppedAt.value = window.performance.now()
        requestAnimationFrame(render)
      }
      pageActive.value = page
      swiperPage.value.openCPage(pageActive.value)
    }

    onMounted(() => {
      window.addEventListener('resize', () => {
        onResize()
        getBall()
        canvasElement.value.width = windowWidth.value
        canvasElement.value.height = windowHeight.value
        context.value = canvasElement.value.getContext('2d')
      })
    })

    return {
      wrap,
      progress,
      backgroundColor,
      textColor,
      getBgColor,
      windowWidth,
      windowHeight,
      onResize,
      getBall,
      canvasElement,
      context,
      ball,
      direction,
      getPage,
      pageActive,
      continueAnimating,
      swiperPage
    }
  }
}
</script>

