<template>
  <section
    id="ball"
    class="section ball section-pop"
  >
  <div class="bg-img-p position-absolute">
    <img
      class="ball__object ball__object__inner ball__object__inner1"
      src="@/assets/img/ball2-1.png"
      data-velocity="7000"
    />
    <img
      class="ball__object ball__object__inner ball__object__inner2"
      src="@/assets/img/ball2-3.png"
      data-velocity="6000"
    />
    <img
      class="ball__object ball__object__inner ball__object__inner3"
      src="@/assets/img/ball2-2.png"
      data-velocity="9500"
    />
  </div>
  </section>
</template>
<script>
import { ref, onMounted, watch } from 'vue'
import $ from 'jquery'
export default {
  props: ['pageActive'],
  setup (props) {
    const page = props

    // 背景ball
    const windowWidth = ref(window.innerWidth)
    const windowHeight = ref(window.innerHeight)
    function onResize () {
      windowWidth.value = window.innerWidth
      windowHeight.value = window.innerHeight
    }

    const bgImgP = ref(null)
    const ballP = ref([])
    function getBall () {
      bgImgP.value = Array.from(document.querySelectorAll('.bg-img-p .ball__object__inner'))
      ballP.value = bgImgP.value.map(item => {
        return {
          itemHeight: item.clientHeight,
          itemWidth: item.clientWidth,
          initialPosition: {
            x: item.offsetLeft,
            y: item.offsetTop
          },
          position: {
            x: item.offsetLeft,
            y: item.offsetTop
          },
          velocity: Number(item.dataset.velocity)
        }
      })
    }
    onMounted(() => {
      setTimeout(() => {
        getBall()
      }, 300)
      window.addEventListener('resize', () => {
        onResize()
        getBall()
      })
    })
    watch(page, (NewVal) => {
      if (NewVal.pageActive !== '') {
        $('.bg-img-p').addClass('Active')
        for (let i = 0; i < ballP.value.length; i++) {
          $(`.bg-img-p.Active .ball__object__inner${i + 1}`).animate({ top: windowHeight.value - ballP.value[i].itemHeight + 'px' }, ballP.value[i].velocity)
          if (ballP.value[i].position.y < -(windowHeight.value / 2)) {
            ballP.value[i].position.y = windowHeight.value
          }
        }
      } else {
        $('.bg-img-p').removeClass('Active')
        $('.bg-img-p .ball__object__inner').stop(true).css({ top: '' })
      }
    }, { deep: true })
    return {
      page,
      ballP,
      getBall,
      windowWidth,
      windowHeight,
      onResize
    }
  }
}
</script>
<style scoped>
.section{
  position: relative;
  width: 100%;
  height: 100%;
}
.section-pop {
  position: absolute;
  top: 0;
  left: 0;
  visibility: hidden;
  z-index: 10;
  background-color: #009fa8;
}
.section-pop.Active{
  visibility: visible;
  background-color: rgb(0, 181, 221);
  transition: background-color 1s ease-in;
}
.ball__object {
  position: absolute;
  z-index: 1;
}
.bg-img-p {
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.bg-img-p .ball__object__inner1 {
  top: calc((168 / 540) * 100%);
  right: calc(100% - 62px);
  width: 237px;
}
.bg-img-p .ball__object__inner2 {
  top: calc((334 / 540) * 100%);
  right: calc(100% - 150px);
  width: 135px;
}
.bg-img-p .ball__object__inner3 {
  top: calc((0 / 540) * 100%);
  left: calc(100% - 200px);
  width: 314px;
}
@media (min-width: 768px) {
  .bg-img-p .ball__object__inner1 {
    top: calc((325 / 900) * 100%);
    right: calc(100% - 303px);
    width: 385px;
  }
  .bg-img-p .ball__object__inner2 {
    top: calc((598 / 900) * 100%);
    right: calc(100% - 449px);
    width: 219px;
  }
  .bg-img-p .ball__object__inner3 {
    top: calc((48 / 900) * 100%);
    left: calc(100% - 370px);
    width: 510px;
  }
}
@media (min-width: 1200px) {
  .bg-img-p .ball__object__inner1 {
    top: calc((91 / 646) * 100%);
    right: calc(100% - 397px);
    width: 385px;
  }
  .bg-img-p .ball__object__inner2 {
    top: calc((366 / 646) * 100%);
    right: calc(100% - 547px);
    width: 219px;
  }
  .bg-img-p .ball__object__inner3 {
    top: calc((28 / 646) * 100%);
    left: calc(100% - 429px);
    width: 396px;
  }
}
@media (min-width: 1600px) {
  .bg-img-p .ball__object__inner1 {
    width: calc(385px * 1.2);
  }
  .bg-img-p .ball__object__inner2 {
    width: calc(219px * 1.2);
  }
  .bg-img-p .ball__object__inner3 {
    width: calc(396px * 1.2);
  }
}
@media (min-width: 1900px) {
  .bg-img-p .ball__object__inner1 {
    width: calc(385px * 1.4);
  }
  .bg-img-p .ball__object__inner2 {
    width: calc(219px * 1.4);
  }
  .bg-img-p .ball__object__inner3 {
    width: calc(396px * 1.4);
  }
}
</style>
